import React, { ReactElement, useEffect, useRef, useState } from "react";
import { lingoConstants, lingoGuessTypes } from "./LingoConstants";
import LingoRow from "./LingoRow";
import { en, en_words } from "./words/en";
import { nl, nl_words } from "./words/nl";
import { nlSt } from "./words/nl-st";

interface KeyboardEvent {
    key: string;
    code: string;
    preventDefault: () => void;
}

export default function Lingo(): ReactElement {
    const inputRef = useRef<HTMLInputElement[]>([]); // Array of input elements to focus and change color
    const boxActive = useRef(0); // The current active box (that is selected)
    const [activeRow, setActiveRow] = useState(0); // Usestate for the active rows
    const [error, setErrorMessage] = useState(""); // Usestate for the error messages
    const [message, setMessage] = useState(""); // Usestate for the general win messages
    const [language, setLanguage] = useState<lingoGuessTypes>("dutch"); // The language that is used
    const [word, setWord] = useState(getWord(language).toUpperCase()); // Usestate for the word that the player is trying to guess

    // Array for each color of the boxes
    const colorArray = Array<string>(
        lingoConstants.NR_OF_LETTERS * lingoConstants.NR_OF_GUESSES
    )
        .fill("var(--navigation-color-septary)")
        .fill(
            "var(--navigation-color-quinary)",
            0,
            lingoConstants.NR_OF_LETTERS
        );
    const colorsBoxes = useRef<string[]>(colorArray);

    // Setter function for the current active box
    const setBoxActive = (value: number) => {
        boxActive.current = value;
    };

    useEffect(() => {
        // Listen for keyboard events
        const listener = (event: KeyboardEvent) => {
            if (event.code === "Enter" || event.code === "NumpadEnter") {
                event.preventDefault();

                // Handle an attempt to make a guess
                handleEnter();
            }
        };

        // Handles an enter press
        function handleEnter() {
            // Get Input
            const input = getRowInput(inputRef.current, activeRow);

            // Validate input
            if (validateInput(input, language)) {
                // If there are errors then remove them
                if (error) setErrorMessage("");
                else handleGuess(input.toUpperCase());
            } else {
                setErrorMessage("You do not have a valid input");
            }
        }

        // Correct guess function
        function correctGuess() {
            // Set the colors to the row to green
            handleStringsArray(activeRow, "green", colorsBoxes.current);

            // Message the user that he has won
            setMessage(`You win! The word was ${word.toLowerCase()}`);
        }

        // Wrong guess function
        function wrongGuess(guess: string) {
            // Calculate the next row
            const nextRow = activeRow + 1;

            // Check if the game is over
            if (nextRow >= lingoConstants.NR_OF_GUESSES) {
                // Tell the user that he has lost
                setErrorMessage(
                    `You have lost! The word was ${word}. Start a new game.`
                );
            } else {
                // word and handle the colors
                handleColors(
                    guess,
                    nextRow,
                    word,
                    colorsBoxes.current,
                    activeRow
                );

                // Focus on the next active box
                boxActive.current = nextRow * lingoConstants.NR_OF_LETTERS;
                inputRef.current[boxActive.current].focus();
            }
            setActiveRow(nextRow);
        }

        // Handle a guess
        function handleGuess(guess: string) {
            // Check if the word is correct
            guess == word ? correctGuess() : wrongGuess(guess);
        }

        // Add the key listener to the document
        document.addEventListener("keydown", listener);
        return () => {
            document.removeEventListener("keydown", listener);
        };
    }, [activeRow, error, boxActive, word, language]);

    // Go to the next box upon typing in a box
    function nextLingoBox(): void {
        const newBox =
            getStartingBoxRow() +
            mod(boxActive.current + 1, lingoConstants.NR_OF_LETTERS);
        setBoxActive(newBox);
        inputRef.current[newBox].focus();
    }

    // Go to the previous box upon pressing backspace
    function previousLingoBox(): void {
        const newBox =
            getStartingBoxRow() +
            mod(boxActive.current - 1, lingoConstants.NR_OF_LETTERS);
        setBoxActive(newBox);
        inputRef.current[newBox].focus();
    }

    // Get the starting box of a row
    function getStartingBoxRow(): number {
        return activeRow * lingoConstants.NR_OF_LETTERS;
    }

    // Start a new game
    function startNewGame() {
        // Reset the colors
        colorsBoxes.current
            .fill("var(--navigation-color-septary)")
            .fill(
                "var(--navigation-color-quinary)",
                0,
                lingoConstants.NR_OF_LETTERS
            );

        // Insert a new word
        setWord(getWord(language).toUpperCase());

        // Remove the letters from each input and put colors in
        inputRef.current.forEach((input) => (input.value = ""));

        // Set the active row to the first row
        setActiveRow(0);

        // Remove error message, and normal message
        if (error) setErrorMessage("");
        if (message) setMessage("");

        // Focus on the first input box
        inputRef.current[0].focus();
    }

    return (
        <div className="container" id="Home">
            <div className="lingo-information">
                <h1 style={{ fontSize: "80px" }}>Wordle</h1>
            </div>
            {error ? (
                <div className="lingo-message declined">
                    <h2>{error}</h2>
                </div>
            ) : null}
            {message ? (
                <div className="lingo-message accepted">
                    <h2>{message}</h2>
                </div>
            ) : null}
            <div className="lingo-game-border">
                {Array.from(
                    { length: lingoConstants.NR_OF_GUESSES },
                    (_, index) => (
                        <LingoRow
                            inputRef={inputRef}
                            rowIndex={index}
                            nextBox={nextLingoBox}
                            setBox={setBoxActive}
                            key={index}
                            previousBox={previousLingoBox}
                            enabled={index == activeRow}
                            colorsBoxes={colorsBoxes}
                        />
                    )
                )}
            </div>
            <div className="lingo-information">
                <h2>Controls</h2>
                <button
                    onClick={() => startNewGame()}
                    className="rgb-button login-button transparent-button white-text"
                >
                    <span>New Game</span>
                </button>
                <div className="lingo-fieldset">
                    <fieldset>
                        <input
                            type="radio"
                            id="dutch"
                            name="language-wordle"
                            onChange={(e) =>
                                setLanguage(e.target.id as lingoGuessTypes)
                            }
                            checked={true}
                        />
                        <label htmlFor="dutch">Dutch</label>
                        <br />
                        <input
                            type="radio"
                            id="english"
                            name="language-wordle"
                            onChange={(e) =>
                                setLanguage(e.target.id as lingoGuessTypes)
                            }
                        />
                        <label htmlFor="english">English</label>
                        <br />
                        <input
                            type="radio"
                            id="straattaal"
                            name="language-wordle"
                            onChange={(e) =>
                                setLanguage(e.target.id as lingoGuessTypes)
                            }
                        />
                        <label htmlFor="straattaal">Dutch (Straattaal)</label>
                        <br />
                    </fieldset>
                </div>
            </div>
            <div className="lingo-information">
                <h2>How to play</h2>
                <p>
                    This game is about trying to guess the word in a few
                    guesses. Only valid words can be inserted into a row. The
                    game starts from the top and goes row by row down to the
                    bottom. Each word that one can guess consists of 5 letters.
                    You can insert a guess by pressing the enter key on the
                    keyboard, it will then move on to the next row and indicate
                    what letters were wrong. No special indication means that
                    the letter is not in the word, a yellow indication means
                    that the letter is in the word but at a different location,
                    and green shows that the letter is indeed in the word and at
                    the correct position.
                </p>
            </div>
        </div>
    );
}

// Applies modulo to a number
function mod(x: number, m: number) {
    return ((x % m) + m) % m;
}

// Remove letter at index i
function removeIndex(input: string, i: number): string {
    return input.slice(0, i) + " " + input.slice(i + 1);
}

// Sets the strings of an array
function handleStringsArray(row: number, color: string, array: string[]) {
    for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
        array[row * lingoConstants.NR_OF_LETTERS + i] = color;
    }
}

// Remove overlapping characters in two strings, and according to the input color a box
function colorGuesses(
    stringOne: string,
    stringTwo: string,
    colorArray: string[],
    startingIndex: number,
    colorRight: string,
    colorWrongLocation: string
) {
    // Color the correct guesses
    for (let i = 0; i < stringOne.length; i++) {
        if (stringOne[i] == stringTwo[i]) {
            // Set the color of the box to the associated color
            colorArray[startingIndex + i] = colorRight;

            // Remove the element from the things to be checked
            stringOne = removeIndex(stringOne, i);
            stringTwo = removeIndex(stringTwo, i);
        }
    }

    // Color the guesses at the wrong location
    for (let i = 0; i < stringOne.length; i++) {
        if (stringOne[i] != " " && stringTwo.includes(stringOne[i])) {
            // Set the color of the box to green
            colorArray[startingIndex + i] = colorWrongLocation;

            // Remove the element from the things to be checked
            const indexRemove = stringTwo.indexOf(stringOne[i]);
            stringTwo =
                stringTwo.slice(0, indexRemove) +
                " " +
                stringTwo.slice(indexRemove + 1);
        }
    }
}

// Function that fetches a random word from the word lists
function getWord(language: lingoGuessTypes): string {
    if (language == "dutch") {
        return nl_words[Math.floor(Math.random() * nl_words.length)];
    } else if (language == "english") {
        return en_words[Math.floor(Math.random() * en_words.length)];
    } else if (language == "straattaal") {
        return nlSt[Math.floor(Math.random() * nlSt.length)];
    }
}

// Validate the input of the user
function validateInput(input: string, language: lingoGuessTypes) {
    if (
        input.length == lingoConstants.NR_OF_LETTERS &&
        (language == "dutch" || language == "straattaal")
    ) {
        return (
            nl.includes(input.toLowerCase()) ||
            nlSt.includes(input.toLowerCase())
        );
    } else if (
        input.length == lingoConstants.NR_OF_LETTERS &&
        language == "english"
    ) {
        return en.includes(input.toLowerCase());
    } else {
        return input.length == lingoConstants.NR_OF_LETTERS;
    }
}

// Get the row input from a certain row
function getRowInput(array: HTMLInputElement[], row: number): string {
    let userInput = "";
    // Loop over the current row
    for (let i = 0; i < lingoConstants.NR_OF_LETTERS; i++) {
        userInput += array[row * lingoConstants.NR_OF_LETTERS + i].value;
    }
    return userInput;
}

// Handle all the colors of a guess
function handleColors(
    guess: string,
    nextRow: number,
    word: string,
    colorArray: string[],
    row: number
) {
    // Color this row to the background color
    handleStringsArray(row, "var(--navigation-color-septary)", colorArray);

    // Color the next row
    handleStringsArray(nextRow, "var(--navigation-color-quinary)", colorArray);

    // check the guesses and provide feedback to the user
    colorGuesses(
        guess,
        word,
        colorArray,
        row * lingoConstants.NR_OF_LETTERS,
        "green",
        "yellow"
    );
}
