import React, { ReactElement, RefObject } from "react";
import LingoBox from "./LingoBox";
import { lingoConstants } from "./LingoConstants";

interface IProps {
    rowIndex: number;
    setBox: (value: number) => void;
    nextBox: () => void;
    previousBox: () => void;
    inputRef: RefObject<HTMLInputElement[]>;
    enabled: boolean;
    colorsBoxes: RefObject<string[]>;
}

export default function LingoRow({
    rowIndex,
    setBox,
    nextBox,
    inputRef,
    previousBox,
    enabled,
    colorsBoxes,
}: IProps): ReactElement {
    return (
        <div className="lingo-row">
            {Array.from(
                { length: lingoConstants.NR_OF_LETTERS },
                (_, index) => (
                    <LingoBox
                        inputRef={inputRef}
                        index={rowIndex * lingoConstants.NR_OF_LETTERS + index}
                        key={rowIndex * lingoConstants.NR_OF_LETTERS + index}
                        setBox={setBox}
                        nextBox={nextBox}
                        previousBox={previousBox}
                        enabled={enabled}
                        colorBoxes={colorsBoxes}
                    />
                )
            )}
        </div>
    );
}
