import React, { ReactElement, RefObject } from "react";

interface IProps {
    index: number;
    setBox: (value: number) => void;
    nextBox: () => void;
    previousBox: () => void;
    inputRef: RefObject<HTMLInputElement[]>;
    enabled: boolean;
    colorBoxes: RefObject<string[]>;
}

export default function LingoBox({
    index,
    setBox,
    nextBox,
    inputRef,
    previousBox,
    enabled,
    colorBoxes,
}: IProps): ReactElement {
    return (
        <input
            style={{ background: colorBoxes.current[index] }}
            type="text"
            className="lingo-box"
            maxLength={1}
            onFocus={() => {
                setBox(index);
            }}
            onChange={(e: React.ChangeEvent<HTMLInputElement>) => {
                if (e.target.value == "") {
                    previousBox();
                } else if (e.target.value) {
                    nextBox();
                }
            }}
            ref={(inputElement) => (inputRef.current[index] = inputElement)}
            disabled={!enabled}
        />
    );
}
